form{
    width: 90%;
    max-width: 300px;
    background: #FFBF00;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 50px 0 0 0;
}

label{
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: #1E1E1F;
    margin-bottom: 26px;
}

textarea{
    background-color: #f9fbfc;
    border: 2px solid #202329;
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
}

textarea::placeholder{
    color: #a5a5a5;
    font-family: 'Monserrat';
    font-weight: 400;
}

textarea:focus{
    outline-color: #61dafa;
}

.TodoForm-buttonContainer{
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.TodoForm-button{
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 5px;
    border: none;
    font-family: 'Monserrat';
    margin: 8px;
}

.TodoForm-button--add{
    margin-top: 16px;
    background: #61dafa;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
}

.TodoForm-button--add:hover{
    transition-duration: 500ms;
    background: rgb(57, 96, 255);
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
}

.TodoForm-button--cancel{
    margin-top: 16px;
    background-color: #CD5C5C;
}

.TodoForm-button--cancel:hover{
    transition-duration: 500ms;
    box-shadow: 0px 5px 25px rgba(255, 79, 94, 0.5);
    background:rgba(176, 0, 15, 0.758);
}